<script setup>
import BaseHeader from '@/components/common/header/baseHeader.vue'
import BaseFooter from '@/components/common/footer/baseFooter.vue'

import { ref} from 'vue';
import axios from "axios";
import { useStore } from "vuex";
import { useRouter } from 'vue-router'
    const store = useStore();

    const strId = ref('');
    const strPass = ref('');

    const router = useRouter();
    sessionStorage.clear();

    const checkLogin = async () => {

        if(strId.value == '') {
            alert('아이디가 없어');
            document.getElementById('userId').focus();
            return false;
        }

        if(strPass.value == '') {
            alert('비번이 없어');
            document.getElementById('userId').focus();
            return false;
        }

        store.commit("changeLoadingStatus", true);

        await axios.post("/restLogin.do",{userId:strId.value, userPass:strPass.value})
        .then((res) => {
            console.log('조회결과',res);
            if(res.data.RESCODE == '0000') {
                sessionStorage.setItem('AccessKeyJwt', res.data.token);
                console.log(sessionStorage.getItem('AccessKeyJwt'));
                router.push('/main');
            } else {
                alert(res.data.RESMSG);
            }
        })
        .catch((res) => {
            console.error("에러", res);
        });
        store.commit("changeLoadingStatus", false);
    }
</script>



<template>
  <BaseHeader title="로그인"></BaseHeader>
  <div>
    <main>
    <div>
        <label for="userId">아이디 &nbsp;&nbsp;&nbsp;: </label>
        <input v-model="strId" id="userId" placeholder="ID입력" /><br>
        <label for="userPass">패스워드 : </label>
        <input id="userPass" type="password" v-model="strPass" placeholder="PASSWORD입력" @keyup.enter="checkLogin()" />
    </div>
    <div style="text-align: right;">
        <button type="button" id="checkBtn" @click="checkLogin">로그인</button><br>
    </div>
  </main>
</div>
<BaseFooter></BaseFooter>
</template>

